import { Box, Button } from '@mui/material'
import Step from '@mui/material/Step'
import StepLabel from '@mui/material/StepLabel'
import Stepper from '@mui/material/Stepper'
import { useTheme } from '@mui/material/styles'
import { ReactNode, useState } from 'react'

interface IStepperRegister {
  step1: JSX.Element
  step2: JSX.Element
  step3: JSX.Element
  onSubmit?: () => void
  isValid?: boolean
  onNext?: (i: number) => void
  onBack?: (i: number) => void
}

export function StepperRegister({
  step1,
  step2,
  step3,
  onSubmit,
  isValid,
  onNext,
  onBack
}: IStepperRegister) {
  const steps: { title: string; component: JSX.Element }[] = [
    { title: 'ประเภทบัญชี', component: step1 },
    { title: 'ข้อมูลผู้ใช้', component: step2 },
    { title: 'เอกสารเพิ่มเติม', component: step3 }
  ]

  const [activeStep, setActiveStep] = useState(0)
  const theme = useTheme()

  const handleNext = () => {
    if (activeStep <= steps.length && isValid) {
      setActiveStep((prevActiveStep) => {
        if (onNext) {
          onNext(prevActiveStep + 1)
        }
        return prevActiveStep + 1
      })
    }
  }

  const handleBack = () => {
    setActiveStep((prevActiveStep) => {
      if (onBack) {
        onBack(prevActiveStep - 1)
      }
      return prevActiveStep - 1
    })
  }

  return (
    <Box sx={{ width: '100%' }}>
      <Stepper activeStep={activeStep}>
        {steps.map((e, index) => {
          const stepProps: { completed?: boolean } = {}
          const labelProps: {
            optional?: ReactNode
          } = {}
          return (
            <Step
              key={index}
              {...stepProps}
              sx={{
                '& .css-1gjpaj2-MuiSvgIcon-root-MuiStepIcon-root.Mui-active': {
                  color: theme.palette.secondary.main
                },
                '& .css-1gjpaj2-MuiSvgIcon-root-MuiStepIcon-root.Mui-completed':
                {
                  color: theme.palette.primary.dark
                }
              }}
            >
              <StepLabel {...labelProps}>{e.title}</StepLabel>
            </Step>
          )
        })}
      </Stepper>
      <Box pb={6}>
        <Box margin='auto' p={4}>{steps[activeStep].component}</Box>
        <Box sx={{ display: 'flex', flexDirection: 'row', pt: 2 }}>
          <Button
            color="secondary"
            variant="contained"
            disabled={activeStep === 0}
            onClick={handleBack}
            sx={{ mr: 1 }}
          >
            Back
          </Button>
          {activeStep === steps.length - 1 ? (
            <Button
              variant="contained"
              color="secondary"
              onClick={() => {
                if (isValid) {
                  onSubmit && onSubmit()
                }
              }}
            >
              Submit
            </Button>
          ) : (
            <Button onClick={handleNext} color="secondary" variant="contained">
              Next
            </Button>
          )}
        </Box>
      </Box>
    </Box>
  )
}
