import * as yup from 'yup'

import {
  Box,
  Container,
  FormGroup,
  RadioGroup,
  Typography
} from '@mui/material'
import { Fragment, useEffect, useLayoutEffect, useState } from 'react'
import { Controller, ErrorOption, useForm } from 'react-hook-form'

import { yupResolver } from '@hookform/resolvers/yup'
import { DatePicker } from '@mui/x-date-pickers/DatePicker'
import axios from 'axios'
import { GeoPoint } from 'firebase/firestore'
import { useNavigate } from 'react-router-dom'
import AutoComplete from '../../components/AutoComplete'
import CheckBox from '../../components/Checkbox'
import Input from '../../components/Input'
import MapBox from '../../components/MapBox'
import Radio from '../../components/Radio'
import { StepperRegister } from '../../components/SteperRegister'
import Upload from '../../components/Upload'
import UploadMulti from '../../components/UploadMulti'
import { PROVINCE_DB } from '../../constants/api'
import { DEFAULT_LOCATION, MAX_DATE } from '../../constants/key'
import { useSweet } from '../../hooks/sweet'
import { BixUser } from '../../models/bixUser'
import TermAndConditionDialog from '../../modules/register/TermAndConditionDialog'
import { authService } from '../../services/authService'
import { useAppSelector } from '../../states/hooks'
import { IProvince } from '../../type'

const RegisterPage = () => {
  const { fbUser, user } = useAppSelector((state) => state.auth)
  const [step, setStep] = useState(0)
  const [open, setOpen] = useState<boolean>(false)

  useLayoutEffect(() => {
    if (!!!localStorage.getItem('acceptCondition')) {
      setOpen(true)
    }
  }, [])

  const email = localStorage.getItem('email')

  const defaultValues: BixUser = {
    type: 'employee',
    personType: 'person',
    jobType: {
      design: false,
      consult: false,
      construct: false
    },
    profile: {
      personCardID: '',
      name: '',
      lastName: '',
      birthDay: MAX_DATE,
      gender: 'male',
      genderDetail: '',
      address: '',
      province: '',
      position: '',
      location: DEFAULT_LOCATION,
      role: 'admin',
      tumbol: '',
      ampher: '',
      postCode: '',
      secretPassword: '',
      personDocUrl: { name: '', url: '' },
      personWithDocUrl: { name: '', url: '' },
      delegateDocURL: { name: '', url: '' }
    },
    tel: '',
    email: email ?? ''
  }

  const personSchem = {
    personCardID: yup
      .string()
      .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
      .max(13, 'ไม่เกิน 13 หลัก')
      .min(13, 'ไม่น้อยกว่า 13 หลัก')
      .required('ใส่เลขประจำตัวประชาชน'),
    name: yup.string().required('ใส่ชื่อ'),
    lastName: yup.string().required('ใส่นามสกุล'),
    gender: yup.string(),
    birthDay: yup.date().required('ใส่วันเดือนปีเกิด'),
    address: yup.string().required('ใส่สถานที่ติดต่อ'),
    province: yup.string().nullable().required('เลือกจังหวัด'),
    tumbol: yup.string().nullable().required('เลือกตำบล/แขวง'),
    ampher: yup.string().nullable().required('เลือกอำเภอ/เขต'),
    postCode: yup.string().nullable().required('เลือกรหัสไปรษณีย์'),
    genderDetail: yup.string().when('gender', {
      is: 'other',
      then: yup.string().required('ใส่เพศ')
    })
  }

  const companySchem = {
    ...personSchem,
    secretPassword: yup
      .string()
      .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
      .max(6, 'ตัวเลข 6 หลัก')
      .min(6, 'ตัวเลข 6 หลัก')
      .required('ใส่รหัส 6 หลัก')
  }

  const schema = yup.object({
    personType: yup.string(),
    profile: yup.object().when('personType', {
      is: 'company',
      then: yup.object(companySchem).required(),
      otherwise: yup.object(personSchem).required()
    }),
    company: yup.object().when('personType', {
      is: 'company',
      then: yup.object({
        comID: yup
          .string()
          .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
          .max(13, 'ไม่เกิน 13 หลัก')
          .min(13, 'ไม่น้อยกว่า 13 หลัก')
          .required('ใส่เลขประจำตัวผู้เสียภาษีอากร'),
        name: yup.string().required('ใส่ชื่อบริษัท'),
        address: yup.string().required('ใส่สถานที่ติดต่อ'),
        province: yup.string().nullable().required('เลือกจังหวัด'),
        tumbol: yup.string().nullable().required('เลือกตำบล/แขวง'),
        // location: yup.string().required('เลือกพิกัด'),
        ampher: yup.string().nullable().required('เลือกอำเภอ/เขต'),
        postCode: yup.string().nullable().required('เลือกรหัสไปรษณีย์'),
        web: yup.string().required('ใส่ชื่อเว็บไซต์')
      })
    }),
    tel: yup
      .string()
      .matches(new RegExp('^[0-9]*$'), 'ใส่ตัวเลขเท่านั้น')
      .required('ใส่เบอร์โทรศัพท์'),
    email: yup.string().email('รูปแบบ Email ไม่ถูกต้อง').required('ใส่ Email')
  })

  const {
    handleSubmit,
    control,
    setValue,
    watch,
    getValues,

    formState: { isValid },
    reset,
    trigger,
    setError
  } = useForm<BixUser>({
    resolver: yupResolver(schema),
    mode: 'all',
    defaultValues
  })

  const zipCode = watch('profile.postCode')
  const zipCodeCom = watch('company.postCode')
  const type = watch('type')
  const personType = watch('personType')
  const gender = watch('profile.gender')

  const [provinceList, setProvinceList] = useState<IProvince[]>([])
  const { alertError } = useSweet()
  const navigate = useNavigate()

  const getProvinceData = async () => {
    const res = await axios(PROVINCE_DB)
    setProvinceList(res.data)
  }
  useEffect(() => {
    if (fbUser && user) {
      navigate('/')
    }
    getProvinceData()
  }, [fbUser, navigate, user])

  const onSubmit = async (data: BixUser) => {
    if (!data.profile.personDocUrl || !data.profile.personWithDocUrl) {
      alertError({ title: 'Fail', text: 'อัพโหลดบัตรและรูปถ่ายคู่บัตรประชาชน' })
      return
    }
    if (data.personType == 'company') {
      if (
        data.company?.companyCerUrl.length === 0 ||
        data.company?.memorandumUrl.length === 0
      ) {
        alertError({
          title: 'Fail',
          text: 'อัพโหลดสำเนานิติบุคคลและสำเนาหนังสือบริคณห์สนธิ'
        })
        return
      }
    }
    try {
      if (fbUser) {
        let dataSave: BixUser
        if (data.personType == 'person') {
          dataSave = {
            ...data,
            profile: {
              ...data.profile,
              role: null,
              secretPassword: data.profile.secretPassword ?? null,
              location: new GeoPoint(
                (data.profile.location as any)['_lat'],
                (data.profile.location as any)['_long']
              )
            },
            company: null
          }
        } else {
          dataSave = {
            ...data,
            profile: {
              ...data.profile,
              role: 'admin',
              location: new GeoPoint(
                (data.profile.location as any)['_lat'],
                (data.profile.location as any)['_long']
              )
            }
          }
        }
        await authService.addUser({ ...dataSave })
      }
    } catch (err: any) {
      alertError({ title: 'Fail', text: err })
    }
  }

  const checkValid = (): boolean => {
    if (step === 1) {
      return isValid
    }
    return true
  }

  const handleCloseDialog = () => {
    setOpen(false)
  }

  return (
    <Fragment>
      <Container>
        <Box pb={2}>
          <Typography variant="h5">ลงทะเบียนผู้ใช้</Typography>
        </Box>
        <form onSubmit={handleSubmit(onSubmit)}>
          <StepperRegister
            isValid={checkValid()}
            step1={
              <Fragment>
                <Controller
                  name="type"
                  control={control}
                  render={({ field: { value, ref, ...rest } }) => (
                    <RadioGroup row value={value}>
                      <Radio
                        {...rest}
                        label="ผู้รับจ้าง"
                        inputRef={ref}
                        value="employee"
                      />
                      <Radio
                        {...rest}
                        label="ผู้จ้าง"
                        inputRef={ref}
                        value="employer"
                      />
                    </RadioGroup>
                  )}
                />
                <Controller
                  name="personType"
                  control={control}
                  render={({ field: { value, ref, ...rest } }) => (
                    <RadioGroup row value={value}>
                      <Radio
                        {...rest}
                        label="บุคคลธรรมดา"
                        inputRef={ref}
                        value="person"
                      />
                      <Radio
                        {...rest}
                        label="นิติบุคคล"
                        inputRef={ref}
                        value="company"
                      />
                    </RadioGroup>
                  )}
                />
              </Fragment>
            }
            step2={
              <Box maxWidth="600px" margin="auto">
                {type === 'employee' && (
                  <FormGroup row>
                    <Controller
                      name="jobType.design"
                      control={control}
                      render={({ field: { ref, value, ...rest } }) => (
                        <CheckBox
                          {...rest}
                          label="ผู้รับจ้างออกแบบ"
                          inputRef={ref}
                          checked={value}
                        />
                      )}
                    />
                    <Controller
                      name="jobType.consult"
                      control={control}
                      render={({ field: { ref, value, ...rest } }) => (
                        <CheckBox
                          {...rest}
                          label="ผู้รับจ้างควบคุมและตรวจสอบงาน/ที่ปรึกษา"
                          inputRef={ref}
                          checked={value}
                        />
                      )}
                    />
                    <Controller
                      name="jobType.construct"
                      control={control}
                      render={({ field: { ref, value, ...rest } }) => (
                        <CheckBox
                          {...rest}
                          label="ผู้รับจ้างก่อสร้าง"
                          inputRef={ref}
                          checked={value}
                        />
                      )}
                    />
                  </FormGroup>
                )}

                {personType == 'company' && (
                  <Fragment>
                    <Box pt={4}>
                      <Typography variant="h5">ข้อมูลนิติบุคคล</Typography>
                    </Box>
                    <Controller
                      name="company.comID"
                      control={control}
                      render={({
                        field: { ref, value, ...rest },
                        fieldState: { error }
                      }) => {
                        return (
                          <Input
                            {...rest}
                            value={value ?? ''}
                            label="เลขทะเบียนนิติบุคคล"
                            inputRef={ref}
                            error={!!error?.type}
                            helperText={error?.message}
                          />
                        )
                      }}
                    />
                    <Controller
                      name="company.name"
                      control={control}
                      render={({
                        field: { ref, value, ...rest },
                        fieldState: { error }
                      }) => {
                        return (
                          <Input
                            {...rest}
                            value={value ?? ''}
                            label="ชื่อนิติบุคคล"
                            inputRef={ref}
                            error={!!error?.type}
                            helperText={error?.message}
                          />
                        )
                      }}
                    />
                    <Controller
                      name="company.address"
                      control={control}
                      render={({
                        field: { ref, ...rest },
                        fieldState: { error }
                      }) => (
                        <Input
                          {...rest}
                          label="สถานที่ติดต่อ"
                          minRows={3}
                          multiline
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                    <Controller
                      name="company.postCode"
                      control={control}
                      render={({
                        field: { ref, ...rest },
                        fieldState: { error }
                      }) => (
                        <AutoComplete
                          label="รหัสไปรษณีย์"
                          options={[
                            ...new Set(provinceList.map((e) => e.PostCodeMain))
                          ]}
                          {...rest}
                          onChangeAT={(e, v) => {
                            setValue('company.postCode', v)
                            setValue('company.province', '')
                            setValue('company.ampher', '')
                            setValue('company.tumbol', '')
                          }}
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                    <Controller
                      name="company.province"
                      control={control}
                      render={({
                        field: { ref, value, ...rest },
                        fieldState: { error }
                      }) => (
                        <AutoComplete
                          label="จังหวัด"
                          options={[
                            ...new Set(
                              provinceList
                                .filter((e) => e.PostCodeMain === zipCodeCom)
                                .map((e) => e.ProvinceThai)
                            )
                          ]}
                          {...rest}
                          onChangeAT={(e, v) => {
                            setValue('company.province', v)
                          }}
                          value={value}
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                    <Controller
                      name="company.ampher"
                      control={control}
                      render={({
                        field: { ref, ...rest },
                        fieldState: { error }
                      }) => (
                        <AutoComplete
                          label="อำเภอ/เขต"
                          options={[
                            ...new Set(
                              provinceList
                                .filter((e) => e.PostCodeMain === zipCodeCom)
                                .map((e) => e.DistrictThai)
                            )
                          ]}
                          {...rest}
                          onChangeAT={(e, v) => {
                            setValue('company.ampher', v)
                          }}
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                    <Controller
                      name="company.tumbol"
                      control={control}
                      render={({
                        field: { ref, ...rest },
                        fieldState: { error }
                      }) => (
                        <AutoComplete
                          label="ตำบล/แขวง"
                          options={[
                            ...new Set(
                              provinceList
                                .filter((e) => e.PostCodeMain === zipCodeCom)
                                .map((e) => e.TambonThai)
                            )
                          ]}
                          {...rest}
                          onChangeAT={(e, v) => {
                            setValue('company.tumbol', v)
                          }}
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                    <MapBox
                      label="เลือกพิกัดที่อยู่"
                      onChange={(lo) => {
                        setValue('company.location', lo)
                      }}
                    />
                    <Controller
                      name="company.web"
                      control={control}
                      render={({
                        field: { ref, value, ...rest },
                        fieldState: { error }
                      }) => {
                        return (
                          <Input
                            {...rest}
                            value={value ?? ''}
                            label="เว็บไซต์"
                            inputRef={ref}
                            error={!!error?.type}
                            helperText={error?.message}
                          />
                        )
                      }}
                    />
                  </Fragment>
                )}
                <Box pt={4}>
                  <Typography variant="h5">ข้อมูลผู้ใช้</Typography>
                </Box>
                <Controller
                  name="profile.personCardID"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      label="เลขประจำตัวประชาชน"
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="profile.name"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      label="ชื่อ"
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="profile.lastName"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      label="นามสกุล"
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                {personType == 'company' && (
                  <Controller
                    name="profile.position"
                    control={control}
                    render={({
                      field: { ref, value, ...rest },
                      fieldState: { error }
                    }) => (
                      <Input
                        {...rest}
                        value={value ?? ''}
                        label="ตำแหน่งงาน"
                        inputRef={ref}
                        error={!!error?.type}
                        helperText={error?.message}
                      />
                    )}
                  />
                )}
                <Controller
                  name="tel"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      label="เบอร์โทรศัพท์"
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="email"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      disabled={true}
                      label="Email"
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                {personType === 'company' && (
                  <Controller
                    name="profile.secretPassword"
                    control={control}
                    render={({
                      field: { ref, ...rest },
                      fieldState: { error }
                    }) => {
                      return (
                        <Input
                          {...rest}
                          label="รหัสลับ 6 ตัว"
                          type="password"
                          inputRef={ref}
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )
                    }}
                  />
                )}
                <Controller
                  name="profile.gender"
                  control={control}
                  render={({ field: { value, ref, ...rest } }) => (
                    <RadioGroup
                      row
                      value={value}
                      onChange={() => {
                        trigger()
                        setValue('profile.genderDetail', '')
                      }}
                    >
                      <Radio
                        {...rest}
                        label="ชาย"
                        inputRef={ref}
                        value="male"
                      />
                      <Radio
                        {...rest}
                        label="หญิง"
                        inputRef={ref}
                        value="female"
                      />
                      <Radio
                        {...rest}
                        label="อื่นๆ"
                        inputRef={ref}
                        value="other"
                      />
                      <Controller
                        name="profile.genderDetail"
                        control={control}
                        render={({
                          field: { ref, value, ...rest },
                          fieldState: { error }
                        }) => (
                          <Input
                            {...rest}
                            value={value ?? ''}
                            placeholder="ระบุ"
                            disabled={gender !== 'other'}
                            inputRef={ref}
                            error={!!error?.type}
                            helperText={error?.message}
                          />
                        )}
                      />
                    </RadioGroup>
                  )}
                />
                <Controller
                  name="profile.birthDay"
                  control={control}
                  render={({
                    field: { ref, value, ...rest },
                    fieldState: { error }
                  }) => (
                    <DatePicker
                      label="วัน/เดือน/ปี เกิด"
                      {...rest}
                      inputRef={ref}
                      value={value}
                      views={['year', 'month', 'day']}
                      disableFuture={true}
                      maxDate={MAX_DATE}
                      disableMaskedInput={true}
                      onChange={(v) => {
                        if (v) {
                          setValue('profile.birthDay', v)
                          const er: ErrorOption = {}
                          setError('profile.birthDay', er)
                        }
                      }}
                      renderInput={(params) => (
                        <Input
                          {...params}
                          placeholder=""
                          error={!!error?.type}
                          helperText={error?.message}
                        />
                      )}
                    />
                  )}
                />
                <Controller
                  name="profile.address"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <Input
                      {...rest}
                      label="สถานที่ติดต่อ/ที่อยู่ปัจจุบัน"
                      minRows={3}
                      multiline
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="profile.postCode"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <AutoComplete
                      label="รหัสไปรษณีย์"
                      options={[
                        ...new Set(provinceList.map((e) => e.PostCodeMain))
                      ]}
                      {...rest}
                      onChangeAT={(e, v) => {
                        setValue('profile.postCode', v)
                        setValue('profile.province', '')
                        setValue('profile.ampher', '')
                        setValue('profile.tumbol', '')
                      }}
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="profile.province"
                  control={control}
                  render={({
                    field: { ref, value, ...rest },
                    fieldState: { error }
                  }) => (
                    <AutoComplete
                      label="จังหวัด"
                      options={[
                        ...new Set(
                          provinceList
                            .filter((e) => e.PostCodeMain === zipCode)
                            .map((e) => e.ProvinceThai)
                        )
                      ]}
                      {...rest}
                      onChangeAT={(e, v) => {
                        setValue('profile.province', v)
                      }}
                      value={value}
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="profile.ampher"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <AutoComplete
                      label="อำเภอ/เขต"
                      options={[
                        ...new Set(
                          provinceList
                            .filter((e) => e.PostCodeMain === zipCode)
                            .map((e) => e.DistrictThai)
                        )
                      ]}
                      {...rest}
                      onChangeAT={(e, v) => {
                        setValue('profile.ampher', v)
                      }}
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <Controller
                  name="profile.tumbol"
                  control={control}
                  render={({
                    field: { ref, ...rest },
                    fieldState: { error }
                  }) => (
                    <AutoComplete
                      label="ตำบล/แขวง"
                      options={[
                        ...new Set(
                          provinceList
                            .filter((e) => e.PostCodeMain === zipCode)
                            .map((e) => e.TambonThai)
                        )
                      ]}
                      {...rest}
                      onChangeAT={(e, v) => {
                        setValue('profile.tumbol', v)
                      }}
                      inputRef={ref}
                      error={!!error?.type}
                      helperText={error?.message}
                    />
                  )}
                />
                <MapBox
                  label="เลือกพิกัดที่อยู่"
                  onChange={(lo) => {
                    setValue('profile.location', lo)
                  }}
                />
              </Box>
            }
            step3={
              <Box maxWidth="600px" margin="auto">
                {personType == 'company' && (
                  <>
                    <Controller
                      name="company.companyCerUrl"
                      control={control}
                      render={({ field: { value } }) => (
                        <UploadMulti
                          label="แนบสำเนานิติบุคคล"
                          objectFit="contain"
                          healperText="*ไฟล์ PDF"
                          imgEx="/images/dbd.gif"
                          url={value}
                          success={(url) => {
                            trigger()
                            setValue('company.companyCerUrl', url)
                          }}
                        />
                      )}
                    />
                    <Controller
                      name="company.memorandumUrl"
                      control={control}
                      render={({ field: { value } }) => (
                        <UploadMulti
                          healperText="*ไฟล์ PDF"
                          objectFit="contain"
                          imgEx="/images/kbh.gif"
                          label="แนบสำเนาหนังสือบริคณห์สนธิ"
                          url={value}
                          success={(url) => {
                            trigger()
                            setValue('company.memorandumUrl', url)
                          }}
                        />
                      )}
                    />
                  </>
                )}
                <Controller
                  name="profile.personDocUrl"
                  control={control}
                  render={({ field: { value } }) => (
                    <Upload
                      label="แนบบัตรประชาชน"
                      url={value}
                      success={(url) => {
                        trigger()
                        setValue('profile.personDocUrl', url)
                      }}
                    />
                  )}
                />
                <Controller
                  name="profile.personWithDocUrl"
                  control={control}
                  render={({ field: { value } }) => (
                    <Upload
                      label="รูปถ่ายคู่บัตรประชาชน"
                      healperText="*ถ่ายรูปให้ชัดเจน ทั้งบัตรและใบหน้า ตามภาพตัวอย่าง"
                      imgEx="/images/face_id.gif"
                      url={value}
                      success={(url) => {
                        trigger()
                        setValue('profile.personWithDocUrl', url)
                      }}
                    />
                  )}
                />
                <Controller
                  name="profile.delegateDocURL"
                  control={control}
                  render={() => {
                    const val = getValues()
                    return (
                      <Upload
                        label="หนังสือมอบอำนาจ"
                        imgEx="/images/dbd.gif"
                        objectFit="contain"
                        healperText="*ไฟล์ PDF หรือ รูปภาพ"
                        fileTypes={['PDF']}
                        url={val.profile.delegateDocURL}
                        success={(url) => {
                          trigger()
                          setValue('profile.delegateDocURL', url)
                        }}
                      />
                    )
                  }}
                />
              </Box>
            }
            onSubmit={() => {
              const data = getValues()
              onSubmit(data)
            }}
            onNext={(i) => {
              setStep(i)
              trigger()
            }}
            onBack={(i) => {
              setStep(i)
              if (i === 0) {
                reset(defaultValues)
              }
            }}
          />
        </form>
      </Container>
      <TermAndConditionDialog open={open} onClose={handleCloseDialog} />
    </Fragment>
  )
}
export default RegisterPage
